import './index.scss';

// VTT åœ°å›¾ URL - ä½¿ç”¨å‰è¯·ä¿®æ”¹ä¸ºä½ çš„å®é™… URL
const VTT_MAP_URL = 'https://ä½ çš„ç”¨æˆ·å.github.io/vtt-map-viewer/index.html';

// è„šæœ¬åŠ è½½æ—¶æ‰§è¡Œ
$(() => {
  toastr.success('ğŸ—ºï¸ VTT åœ°å›¾æ‚¬æµ®çª—å·²åŠ è½½');

  // åˆ›å»ºæµ®åŠ¨æŒ‰é’®
  createFloatingButton();

  // ç›‘å¬å¿«æ·é”® Ctrl+M æ‰“å¼€åœ°å›¾
  $(document).on('keydown', e => {
    if (e.ctrlKey && e.key === 'm') {
      e.preventDefault();
      toggleVTTWindow();
    }
  });
});

// è„šæœ¬å¸è½½æ—¶æ‰§è¡Œ
$(window).on('pagehide', () => {
  $('#vtt-map-button').remove();
  $('#vtt-floating-window').remove();
  toastr.info('ğŸ—ºï¸ VTT åœ°å›¾æ‚¬æµ®çª—å·²å¸è½½');
});

/**
 * åˆ›å»ºæµ®åŠ¨æŒ‰é’®
 */
function createFloatingButton(): void {
  // æ£€æŸ¥æ˜¯å¦å·²å­˜åœ¨
  if ($('#vtt-map-button').length > 0) {
    return;
  }

  const button = $('<button>')
    .attr('id', 'vtt-map-button')
    .attr('title', 'æ‰“å¼€ VTT åœ°å›¾ (Ctrl+M)')
    .addClass('vtt-map-button')
    .html(
      `
      <svg width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
        <path d="M20.5 3l-.16.03L15 5.1 9 3 3.36 4.9c-.21.07-.36.25-.36.48V20.5c0 .28.22.5.5.5l.16-.03L9 18.9l6 2.1 5.64-1.9c.21-.07.36-.25.36-.48V3.5c0-.28-.22-.5-.5-.5zM15 19l-6-2.11V5l6 2.11V19z"/>
      </svg>
      <span>åœ°å›¾</span>
    `,
    )
    .on('click', () => {
      toggleVTTWindow();
    });

  $('body').append(button);
}

/**
 * åˆ‡æ¢ VTT çª—å£æ˜¾ç¤º/éšè—
 */
function toggleVTTWindow(): void {
  const existingWindow = $('#vtt-floating-window');

  if (existingWindow.length > 0) {
    if (existingWindow.is(':visible')) {
      existingWindow.hide();
    } else {
      existingWindow.show();
    }
  } else {
    createVTTWindow();
  }
}

/**
 * åˆ›å»º VTT æ‚¬æµ®çª—
 */
function createVTTWindow(): void {
  const floatingWindow = $('<div>').attr('id', 'vtt-floating-window').addClass('vtt-floating-window');

  // æ ‡é¢˜æ 
  const titleBar = $('<div>').addClass('vtt-title-bar');

  const title = $('<div>').addClass('vtt-title').html(`
      <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
        <path d="M20.5 3l-.16.03L15 5.1 9 3 3.36 4.9c-.21.07-.36.25-.36.48V20.5c0 .28.22.5.5.5l.16-.03L9 18.9l6 2.1 5.64-1.9c.21-.07.36-.25.36-.48V3.5c0-.28-.22-.5-.5-.5zM15 19l-6-2.11V5l6 2.11V19z"/>
      </svg>
      <span>VTT åœ°å›¾æŸ¥çœ‹å™¨</span>
    `);

  // æ§åˆ¶æŒ‰é’®ç»„
  const controls = $('<div>').addClass('vtt-controls');

  // æœ€å°åŒ–æŒ‰é’®
  const minimizeBtn = $('<button>')
    .addClass('vtt-control-btn vtt-minimize-btn')
    .attr('title', 'æœ€å°åŒ–')
    .html('â–')
    .on('click', () => {
      const content = floatingWindow.find('.vtt-content');
      const isMinimized = content.hasClass('minimized');

      if (isMinimized) {
        content.removeClass('minimized');
        floatingWindow.css('height', '80vh');
        minimizeBtn.html('â–');
      } else {
        content.addClass('minimized');
        floatingWindow.css('height', 'auto');
        minimizeBtn.html('â¬œ');
      }
    });

  // å…¨å±æŒ‰é’®
  const fullscreenBtn = $('<button>')
    .addClass('vtt-control-btn vtt-fullscreen-btn')
    .attr('title', 'å…¨å±')
    .html('â›¶')
    .on('click', () => {
      const isFullscreen = floatingWindow.hasClass('fullscreen');

      if (isFullscreen) {
        floatingWindow.removeClass('fullscreen');
        fullscreenBtn.html('â›¶');
      } else {
        floatingWindow.addClass('fullscreen');
        fullscreenBtn.html('ğŸ——');
      }
    });

  // å…³é—­æŒ‰é’®
  const closeBtn = $('<button>')
    .addClass('vtt-control-btn vtt-close-btn')
    .attr('title', 'å…³é—­ (Esc)')
    .html('âŒ')
    .on('click', () => {
      floatingWindow.hide();
    });

  controls.append(minimizeBtn, fullscreenBtn, closeBtn);
  titleBar.append(title, controls);

  // å†…å®¹åŒºåŸŸ
  const content = $('<div>').addClass('vtt-content');

  // åŠ è½½ä¸­æç¤º
  const loading = $('<div>').addClass('vtt-loading').html(`
      <div class="spinner"></div>
      <p>æ­£åœ¨åŠ è½½ VTT åœ°å›¾...</p>
    `);

  // iframe
  const iframe = $('<iframe>')
    .addClass('vtt-iframe')
    .attr('src', VTT_MAP_URL)
    .attr('allow', 'fullscreen')
    .on('load', () => {
      loading.fadeOut();
      toastr.success('ğŸ—ºï¸ åœ°å›¾åŠ è½½å®Œæˆ');
    })
    .on('error', () => {
      loading.html(`
        <div class="error-icon">âš ï¸</div>
        <p>åœ°å›¾åŠ è½½å¤±è´¥</p>
        <small>è¯·æ£€æŸ¥ URL æ˜¯å¦æ­£ç¡®</small>
      `);
      toastr.error('âŒ åœ°å›¾åŠ è½½å¤±è´¥');
    });

  content.append(loading, iframe);
  floatingWindow.append(titleBar, content);
  $('body').append(floatingWindow);

  // ä½¿çª—å£å¯æ‹–åŠ¨
  makeWindowDraggable(floatingWindow, titleBar);

  // ä½¿çª—å£å¯è°ƒæ•´å¤§å°
  makeWindowResizable(floatingWindow);

  // ESC é”®å…³é—­çª—å£
  $(document).on('keydown.vtt-window', e => {
    if (e.key === 'Escape' && floatingWindow.is(':visible')) {
      floatingWindow.hide();
    }
  });

  // æ˜¾ç¤ºçª—å£
  floatingWindow.fadeIn(300);
}

/**
 * ä½¿çª—å£å¯æ‹–åŠ¨
 */
function makeWindowDraggable(window: JQuery, handle: JQuery): void {
  let isDragging = false;
  let offsetX = 0;
  let offsetY = 0;

  handle.on('mousedown', e => {
    // é˜²æ­¢åœ¨æŒ‰é’®ä¸Šå¼€å§‹æ‹–åŠ¨
    if ($(e.target).closest('.vtt-control-btn').length > 0) {
      return;
    }

    isDragging = true;
    const windowPos = window.offset()!;
    offsetX = e.pageX - windowPos.left;
    offsetY = e.pageY - windowPos.top;

    handle.css('cursor', 'grabbing');
    $('body').addClass('vtt-dragging');
  });

  $(document).on('mousemove.vtt-drag', e => {
    if (isDragging && !window.hasClass('fullscreen')) {
      const x = e.pageX - offsetX;
      const y = e.pageY - offsetY;

      // é™åˆ¶åœ¨çª—å£èŒƒå›´å†…
      const maxX = $(window).width()! - window.outerWidth()!;
      const maxY = $(window).height()! - window.outerHeight()!;

      window.css({
        left: Math.max(0, Math.min(x, maxX)) + 'px',
        top: Math.max(0, Math.min(y, maxY)) + 'px',
        transform: 'none',
      });
    }
  });

  $(document).on('mouseup.vtt-drag', () => {
    if (isDragging) {
      isDragging = false;
      handle.css('cursor', 'move');
      $('body').removeClass('vtt-dragging');
    }
  });
}

/**
 * ä½¿çª—å£å¯è°ƒæ•´å¤§å°
 */
function makeWindowResizable(window: JQuery): void {
  // åˆ›å»ºè°ƒæ•´å¤§å°çš„æ‰‹æŸ„
  const resizeHandles = ['n', 'e', 's', 'w', 'ne', 'se', 'sw', 'nw'];

  resizeHandles.forEach(direction => {
    const handle = $('<div>').addClass(`vtt-resize-handle vtt-resize-${direction}`).attr('data-direction', direction);

    window.append(handle);
  });

  let isResizing = false;
  let resizeDirection = '';
  let startX = 0;
  let startY = 0;
  let startWidth = 0;
  let startHeight = 0;
  let startLeft = 0;
  let startTop = 0;

  window.find('.vtt-resize-handle').on('mousedown', e => {
    if (window.hasClass('fullscreen')) {
      return;
    }

    isResizing = true;
    resizeDirection = $(e.currentTarget).attr('data-direction')!;
    startX = e.pageX;
    startY = e.pageY;
    startWidth = window.width()!;
    startHeight = window.height()!;
    startLeft = window.offset()!.left;
    startTop = window.offset()!.top;

    $('body').addClass('vtt-resizing');
    e.preventDefault();
  });

  $(document).on('mousemove.vtt-resize', e => {
    if (isResizing) {
      const deltaX = e.pageX - startX;
      const deltaY = e.pageY - startY;

      let newWidth = startWidth;
      let newHeight = startHeight;
      let newLeft = startLeft;
      let newTop = startTop;

      // æ ¹æ®æ–¹å‘è°ƒæ•´å¤§å°
      if (resizeDirection.includes('e')) {
        newWidth = startWidth + deltaX;
      }
      if (resizeDirection.includes('w')) {
        newWidth = startWidth - deltaX;
        newLeft = startLeft + deltaX;
      }
      if (resizeDirection.includes('s')) {
        newHeight = startHeight + deltaY;
      }
      if (resizeDirection.includes('n')) {
        newHeight = startHeight - deltaY;
        newTop = startTop + deltaY;
      }

      // é™åˆ¶æœ€å°å°ºå¯¸
      newWidth = Math.max(400, newWidth);
      newHeight = Math.max(300, newHeight);

      window.css({
        width: newWidth + 'px',
        height: newHeight + 'px',
        left: newLeft + 'px',
        top: newTop + 'px',
      });
    }
  });

  $(document).on('mouseup.vtt-resize', () => {
    if (isResizing) {
      isResizing = false;
      $('body').removeClass('vtt-resizing');
    }
  });
}
